import './App.css'
import './css/entry_container.css'
import './css/dashboard_container.css'

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <Provider store={store} >
       <App />
    </Provider>
  </React.StrictMode>
);


